import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let lifeData = {};

try{
	// lifeDataAPP
	lifeData = uni.getStorageSync('lifeData');
}catch(e){
	
}

// APPstate
let saveStateKeys = ['sysconfigMap', 'app_show_pic_version', 'referrer', 'token', 'uid'];

// 
const saveLifeData = function(key, value){
	// 
	if(saveStateKeys.indexOf(key) != -1) {
		// lifeData
		let tmp = uni.getStorageSync('lifeData');
		// APPlifeData{}
		tmp = tmp ? tmp : {};
		tmp[key] = value;
		// lifeData
		uni.setStorageSync('lifeData', tmp);
	}
}
const store = new Vuex.Store({
	state: {
		// lifeDatastate
		// vuex_versionlifeData.vuex_version
		sysconfigMap: lifeData.sysconfigMap ? lifeData.sysconfigMap : {
			mallName: '',
			shopMod: 0
		},
		app_show_pic_version: lifeData.app_show_pic_version ? lifeData.app_show_pic_version : '',
		referrer: lifeData.referrer ? lifeData.referrer : '',
		token: lifeData.token ? lifeData.token : 'c22cd584-9b63-4fdf-8ff3-522d751ef9a4',
		uid: lifeData.uid ? lifeData.uid : '1351478',
	},
	mutations: {
		$uStore(state, payload) {
			// stateuser.info.score = 1
			let nameArr = payload.name.split('.');
			let saveKey = '';
			let len = nameArr.length;
			if(len >= 2) {
				let obj = state[nameArr[0]];
				for(let i = 1; i < len - 1; i ++) {
					obj = obj[nameArr[i]];
				}
				obj[nameArr[len - 1]] = payload.value;
				saveKey = nameArr[0];
			} else {
				// state
				state[payload.name] = payload.value;
				saveKey = payload.name;
			}
			// 
			saveLifeData(saveKey, state[saveKey])
		}
	}
})

export default store
